import React from 'react';
import "./about.css";
import Card from "./Card";
import data from "./data";

//import logo from '../image/cablelogo.jpeg';
const About = () => {
   
    const card = data.map((item)=>{
        return <Card   des1={item.des1} des={item.des} name={item.name} />;
    })
    return (
        
        <div>
            <h1 className="heading1">Let Know Our Growth  of Business</h1>
            <div className="header_underline"></div>
            <div className="wrapper">{card}</div>
        </div>
    );
}

export default About;
