import React from 'react';
import "./contact.css";
//import contactconfig from 'contactconfig';
import {Container,Row,Col} from "react-bootstrap";
import { contactConfig } from './contactconfig';
import { useState } from "react";
import axios from 'axios';

const Contact = () => {

  const [status, setStatus] = useState("Submit");
  const handleSubmit = async (e) => {
    e.preventDefault();
    setStatus("Sending...");
    const { name, email, message } = e.target.elements;
    let details = {
      name: name.value,
      email: email.value,
      message: message.value,
    };
    let response = await fetch("http://localhost:5000/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(details),
    });
    setStatus("Submit");
    let result = await response.json();
    alert(result.status);
  };
  
  return (
   <Container>
      <Row className="head mb-5  mt=3">
          <Col lg='g'>
              <h3 className='display-8 contat mb-2'>
                  Contact Me
              </h3>
              <div className="header_underline"></div>
          </Col>
      </Row>
      <Row className="sec_sp">
        <Col lg='5' className='mb-3'>
          <h3 className="color sec py-4">Get In Touch</h3>
              <address className="address">
                    <strong>Email: sarathycables@gmail.com</strong>
                    <br/><br/>
                    <p><strong>Phone: +91 7904207626</strong></p>
                    <p>{contactConfig.description}</p>
              </address>
          
        </Col>
        <Col lg="7" className='d-flex align-items-center'>
          <form onSubmit={handleSubmit} className='contact_form w-100'>
            <Row>
                  <Col lg='6'className='form-group'>
                      <input className='form-control rounded-0'
                      id="name"
                      name="name"
                      placeholder='Name'
                      type="text"
                      
                      />
                </Col>
               
                <Col lg='6'className='form-group'>
                      <input className='form-control rounded-0'
                      id="email"
                      name="email"
                      placeholder='Email'
                      type="email"
                      
                      />
          </Col>
          </Row>
          <br/>
          <textarea 
                    className='form-control rounded-0'
                    id="message"
                    name="message"
                    placeholder='Message'
                    rows="5"
                    
                    >

          </textarea>
        <br/>
        <Row>
            <Col lg='12' className='form-group'>
              <button 
              
              className='btn ac_btn' 
              type="Submit">{status}</button>
            </Col>
        </Row>
          </form>
        </Col>
        
          
      </Row>
   </Container>
  );
}

export default Contact;
