import React,{useState}from 'react';
import './header.css';
import Menu from '@mui/icons-material/Menu';
import Close from '@mui/icons-material/Close';
import logo from '../image/cablelogo.jpeg';
import {NavLink} from 'react-router-dom';
import Slider from '../slider/Slider';
const Header = () => {
  
    const [isMobileMenu,setIsMobileMenu] = useState(false);


  return (
    <>
      <nav className="navbar">
            <img className="logo" src={logo}  alt="cablelogo" />
            <h2 className='name'>S.P.Sarathy Cables and Networks</h2>
            
      <ul className={isMobileMenu ? "nav-links-mobile" :"nav-links"}
      onClick={()=>setIsMobileMenu(false)}
      >
            <NavLink to ='/' className='home'><li>Home</li></NavLink>
            <NavLink to ='/about'className='about'><li>About</li></NavLink>
            <NavLink to ='/services' className='services'><li>Services</li></NavLink>
            <NavLink to ='/contact'className='contact'><li>Contact</li></NavLink>
      </ul> 
 
    <button className="mobile-menu-icon"
      onClick={()=>setIsMobileMenu(!isMobileMenu)}
    >
          {isMobileMenu ? <Close/>: <Menu />}
    </button>

    </nav>
    <Slider />
    </>
  );
}

export default Header;
